body {
  background-color: #282c34;
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
}

.App {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 100%;
  margin: 20px auto;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

.card {
  animation: fadeIn 0.5s ease-in-out;
  background-color: #424242;
  border-radius: 15px;
  width: 50vw;
  height: 70vh;
  margin: 10px;
  padding: 20px;
  color: #ffffff;
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  transition: transform 0.3s;
}

.card:hover {
  transform: scale(1.05);
}

h1 {
  color: #61dafb;
  text-align: center;
  width: 100%;
}

h2 {
  color: #61dafb;
  margin-bottom: 20px;
}

p {
  font-size: 14px;
  line-height: 1.6;
}


.education-block {
  background-color: #333;
  border-radius: 15px;
  padding: 15px;
  margin-bottom: 10px;
}

.education-block h3 {
  color: #61dafb;
  margin-bottom: 5px;
}

.education-block p {
  margin-bottom: 5px;
}


.info-row {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.info-row h3 {
  margin-left: 10px;
}

.experience-block {
  background-color: #333;
  border-radius: 15px;
  padding: 15px;
  margin-bottom: 10px;
}

.experience-block h3 {
  color: #61dafb;
  display: flex;
  align-items: center;
  margin-bottom: 5px;
}

.experience-block h3 svg {
  margin-right: 10px;
}

.experience-block p {
  margin-bottom: 5px;
}

.interests-row {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.interests-row h3 {
  margin-left: 10px;
}
